@import 'src/colors';

.contacts-container {
    background-color: $backgroundDark;
    padding: 4em 0;
    display: flex;
    flex-direction: column;
    align-items: center;

    > div {
        width: 95%;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    h2 {
        letter-spacing: 0.3em;
        margin-bottom: 2em;
        text-align: center;
    }

    a {
        text-decoration: none;
        color: $fontColor;
    }

    .contacts-item {
        background-color: $backgroundLigth;
        box-shadow: $shadow;
        height: 65px;
        border-radius: 8px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        transition-duration: 100ms;
        margin-bottom: 0.5em;
        width: 100%;

        &:hover {
            cursor: pointer;
        }

        img {
            width: 30px;
            height: 30px;
            margin: 0 1em;
        }
    }

    .back-home {
        color: $fontColor;
        font-size: 1em;
        text-transform: uppercase;
        padding: 0.5em 1em;
        margin: 4em 0 1em 0;
        border-radius: 5px;
        border: none;
        background: none;
        opacity: 0.8;
        transition-duration: 300ms;
        &:hover {
            opacity: 1;
            background-color: $backgroundLigth;
            transform: scale(1.1);
            cursor: pointer;
        }
    }
}

@media screen and (min-width: 600px) {
    .contacts-container {
        > div {
            width: 470px;
        }
    }
}
