@import 'src/colors';

.intro-question {
    height: 90vh;
    background-color: $backgroundDark;
    display: flex;
    justify-content: space-around;
    align-items: center;
    text-align: center;

    h1 {
        letter-spacing: 0.3em;
        margin: 1em;
    }
}

.intro {
    background-color: $backgroundLigth;
    text-align: center;
    display: flex;
    justify-content: space-around;

    h1,
    h2 {
        letter-spacing: 0.3em;
    }

    .intro__content {
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding: 7em 0;
        max-width: 700px;
        align-items: center;

        h2 {
            margin-top: 1em;
        }

        img {
            border-radius: 50%;
            width: 200px;
            margin: 1em 0;
            box-shadow: $shadow;
        }
    }
}

@media screen and (min-width: 820px) {
    .intro-question {
        height: 95vh;
    }

    .intro__content {
        padding: 4em 0;
    }
}
