@import 'src/colors';

.workslist-container {
    min-height: 100vh;
    background-color: $backgroundLigth;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    h2 {
        padding-top: 4em;
    }

    .list {
        width: 95%;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;

        .list__item {
            background-color: #203647;
            transition-duration: 300ms;
            margin-top: 1.5em;
            box-shadow: $shadow;
            border-radius: 8px;
            display: flex;
            flex-direction: column;
            justify-content: space-between;

            h3 {
                font-weight: bold;
            }

            &:last-child {
                margin-bottom: 4em;
            }

            img {
                width: 100%;
                border-top-left-radius: 8px;
                border-top-right-radius: 8px;
            }

            .list__item__description {
                h3 {
                    padding: 1em 0 0 1em;
                }
            }

            a {
                text-decoration: none;
                text-transform: uppercase;
                margin: 1em;
                padding: 0.3em 0.5em;
                color: #58b4ee;
                align-self: center;

                &:visited {
                    color: #58b4ee;
                }
                &:hover {
                    cursor: pointer;
                    background-color: #244a5f;
                }
            }
        }
    }
}

.project-description {
    background-color: $backgroundDark;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    justify-content: flex-start;
    position: relative;
    padding: 0 0 1em 0;

    :first-child {
        width: 350px;
    }

    img {
        width: 350px;
    }

    h1 {
        margin: 2em 0 0.5em 0;
    }

    p {
        padding: 0.5em 0;
    }

    a {
        color: $fontColor;
        text-decoration: none;
        cursor: pointer;
        &:visited {
            color: inherit;
        }
        width: inherit;
    }

    ul {
        border: #4da8da 1px solid;
        margin: 0.5em 0;
        li {
            font-size: 0.9em;
            line-height: 1.5em;
            color: gold;
            &:last-child {
                margin-bottom: 0.5em;
            }
        }
    }

    .project-link {
        border-bottom: #4da8da 1px solid;
        padding: 0.5em;
        margin-right: 0.5em;
        &:hover {
            opacity: 0.5;
        }
    }

    .home-link {
        position: absolute;
        bottom: 2em;
        right: 0.5em;
        padding: 0.5em 1em;
        opacity: 0.8;
        transition-duration: 300ms;
        &:hover {
            transform: scale(1.1);
            opacity: 1;
            background-color: #4da8da;
            cursor: pointer;
        }
        margin: 1em;
    }
}

@media screen and (min-width: 600px) {
    .list__item {
        width: 350px;
        height: 400px;
        margin-left: 1em;
    }

    .project-description {
        img {
            width: 450px;
        }

        :first-child {
            width: 450px;
        }
    }
}
